export class AllocRegenerateComponentpg {  
  constants: Constants = new Constants();
  session! : MedicoSession;
  allocationId:string="0";
  form:FormGroup;
  constructor(
    public dialogRef: MatDialogRef<AllocRegenerateComponentpg>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public fb: FormBuilder,
    public medicoUtility: MedicoUtility,
    public medicoService: MedicoService,
    public allocationService: AllocationService,
    public dialog: MatDialog
  ) {
    this.form = this.fb.group({
      entredBrands:new FormControl(""),
    
    });
    
  }
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  displayedColumns: string[] = ["select","dateOfAlloc","division","docNumber","company"] ;
  @ViewChild(MatSort) sort!: MatSort;


 

  public onSelect(row:any){
    this.allocationId=row.allocationId;
   }
  confirmRegenerate(){
    if(this.allocationId!="0"){
      this.medicoService.confirmBox("Confirmation", "Regeneration will delete all modified data?",
      "Ok", "Cancel")
      .subscribe(result => {
        if (result == true) {
          this.dialogRef.close({allocationId:this.allocationId});
        }
      });
    }
    else{
      this.medicoService.popup("Message","Select document to Regenerate")
    }
  }
}
