import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { ChildEssentialsModule } from './child.essential.module';
import { DirectivesModule } from './directives.module';
import { AnnualAllocationEntryComponent, AnnualDetailComponent } from '../components/transaction/annual-allocation-entry/annual-allocation-entry.component';
import { MonthlyAllocationEntryComponent } from '../components/transaction/monthly-allocation-entry/monthly-allocation-entry.component';
// import { AllocEntModifyComponentpg, AllocRegenerateComponentpg, FieldstaffDetailsComponentpg, MonthlyAllocationEntryPgComponent } from '../components/transaction/monthly-allocation-entry-pg/monthly-allocation-entry-pg.component';
import { AuthenticationGuard } from '../guards/authentication.guard/authentication.guard';
import { AllocEntModifyComponentpg, AllocRegenerateComponentpg, FieldstaffDetailsComponentpg, MonthlyAllocationEntryPgComponent } from '../components/transaction/monthly-allocation-entry-pg/monthly-allocation-entry-pg.component';

const routes: Routes = [
  { path: "entry", component: AnnualAllocationEntryComponent, canActivate: [ AuthenticationGuard ] },
  { path: "monthlyEntry", component: MonthlyAllocationEntryComponent, canActivate: [ AuthenticationGuard ] },
  { path: "monthlyEntrypg", component: MonthlyAllocationEntryPgComponent, canActivate: [ AuthenticationGuard ] },
]

@NgModule({
  imports: [ 
    RouterModule.forChild(routes), 
    CommonModule,
    ChildEssentialsModule, 
    DirectivesModule
  ],
  exports: [
    RouterModule
  ],
  declarations: [
    AnnualAllocationEntryComponent,
    AnnualDetailComponent,
    MonthlyAllocationEntryPgComponent,
    AllocEntModifyComponentpg,
    FieldstaffDetailsComponentpg,
    AllocRegenerateComponentpg,
  ],

})
export class AllocationModule { }
